import { useState, useEffect } from "react";
import { Grommet, grommet, Box, Meter, Text, Button, Stack } from "grommet";

import "./App.css";

const START_DATE = new Date("2024-02-01");

function getMondaysBetween(startDate, endDate) {
  function startOfDay(date) {
    return new Date(date.setHours(0, 0, 0, 0));
  }

  startDate = startOfDay(startDate);
  endDate = startOfDay(endDate);

  // Set to the next Monday if the start date is not a Monday
  const day = startDate.getDay();
  startDate.setDate(startDate.getDate() + ((7 - day + 1) % 7));

  const res = [];
  while (startDate <= endDate) {
    res.push(new Date(startDate));
    startDate.setDate(startDate.getDate() + 7);
  }

  return res;
}

const dates = getMondaysBetween(START_DATE, new Date())
  .reverse()
  .map((date) => date.toDateString());

function App() {
  const [weeks, setWeeks] = useState(
    dates.reduce((acc, date) => {
      acc[date] = parseInt(localStorage.getItem(date) ?? 0);
      return acc;
    }, {}),
  );

  useEffect(() => {
    dates.forEach((date) => {
      localStorage.setItem(date, weeks[date]);
    });
  }, [weeks]);

  function createSetWeek(week) {
    return (value) => {
      setWeeks((prev) => ({ ...prev, [week]: value }));
    };
  }

  return (
    <Grommet theme={grommet} style={{ fontFamily: "monospace" }}>
      <Box fill="horizontal">
        {Object.entries(weeks).map(([week, value]) => (
          <Week
            key={week}
            week={week}
            value={value}
            setWeek={createSetWeek(week)}
          />
        ))}
        <Button
          label="Reset"
          onClick={() => {
            localStorage.clear();
            window.location.reload();
          }}
        />
      </Box>
    </Grommet>
  );
}

function Week({ week, value, setWeek }) {
  return (
    <Stack anchor="top-right">
      <Meter
        color={value >= 3 ? "focus" : "accent-4"}
        type="bar"
        value={(100 / 3) * value}
        size="full"
        thickness="xlarge"
      />
    </Stack>
  );
}

export default App;
